/** @format */

.image {
  height: 100vh;
  opacity: 0.7;
}
.image-container {
  position: relative;
}
.btn {
  left: 50%;
  top: 50%;
  position: absolute;
  height: 50px;
  width: 150px;
  border-radius: 22px;
  text-decoration: none;
  font-size: 18px;
  background-color: black;
  color: white;
}
.btn:hover {
  color: black;
  background-color: rgb(243, 243, 243);
}
