/** @format */

.card {
  width: 25%;
  border-radius: 10px;

  margin: 0 auto;
  margin-top: 50px;
  box-shadow: 0 5px 10px rgba(154, 160, 185, 0.05),
    0 15px 40px rgba(166, 173, 201, 0.2);
}
.details {
  padding: 20px;
}
.btnNext {
  height: 40px;
  width: 100px;
  border: none;
  border-radius: 10px;
  text-decoration: none;
  font-size: 18px;
  background-color: black;
  color: white;
}
.btnNext:hover {
  color: black;
  background-color: #3297e4;
}
